<template>
    
    <v-dialog v-model="mainDlg" :persistent="persistent" transition="dialog-top-transition">
        <slot></slot>
    </v-dialog>

</template>

<script>

    export default {
        name: 'GenericDlg',

        props: {
            show: Boolean,
            persistent: {
                type: Boolean,
                default: false
            }
        },

        computed: {
            mainDlg: {
                get: function () { return this.show; },
                set: function () { this.$emit('done'); }
            }
        }

    }

</script>
